<template name="about">
	<view>
		<view class="cu-bar bg-black search">
			<img-cache v-show="avatarimgLoaded" class="cu-avatar round" :src="$avatarsmall" @tap="tabSelect" data-id="0"
				@load="onSuccessImg()" />
			</img-cache>
			<img-cache v-show="!avatarimgLoaded" class="cu-avatar round" :src="$avatarsmalldefault" @tap="tabSelect"
				data-id="0" style="margin-left: 11px;" />
			</img-cache>
			<view class="content">
				个人中心
			</view>
			<view class="action">
				<text class="cuIcon-search"></text>
			</view>
		</view>
		<scroll-view scroll-x class="bg-green nav text-center">
			<view class="cu-item">
				用户名：{{$username}}
			</view>
			<view class="cu-item" :class="1==TabCur?'text-white cur':''" @tap="tabSelect" data-id="1">
				<text class="cuIcon-formfill"></text> 帖子
			</view>
			<view class="cu-item" :class="2==TabCur?'text-white cur':''" @tap="tabSelect" data-id="2">
				<text class="cuIcon-favorfill"></text> 收藏
			</view>
			<view class="cu-item" :class="3==TabCur?'text-white cur':''" @tap="tabSelect" data-id="3">
				<text class="cuIcon-newshotfill"></text> 足迹
			</view>
		</scroll-view>
		<view class="cu-bar bg-white solid-bottom margin-top">
			<view class="action">
				<text v-if="myinfoprompt>0" class="cuIcon-title text-orange "></text> 快捷信息
			</view>
		</view>
		<view class="cu-list grid col-3 no-border">
			<view class="cu-item" @click="messageclick">
				<view class="cuIcon-cardboardfill text-red">
					<view class="cu-tag badge" v-if="this.mynewpm!=0">
						<block v-if="this.mynewpm!=1">{{this.mynewpm>99?'99+':this.mynewpm}}</block>
					</view>
				</view>
				<text>消息</text>
			</view>
			<view class="cu-item" @click="messageclick">
				<view class="cuIcon-cardboardfill text-red">
					<view class="cu-tag badge" v-if="this.mynewpm!=0">
						<block v-if="this.mynewpm!=1">{{this.mynewpm>99?'99+':this.mynewpm}}</block>
					</view>
				</view>
				<text>提醒</text>
			</view>
			<view class="cu-item" @click="messageclick">
				<view class="cuIcon-cardboardfill text-red">
					<view class="cu-tag badge" v-if="this.mynewpm!=0">
						<block v-if="this.mynewpm!=1">{{this.mynewpm>99?'99+':this.mynewpm}}</block>
					</view>
				</view>
				<text>留言</text>
			</view>
			<view class="cu-item" @click="messageclick">
				<view class="cuIcon-cardboardfill text-red">
					<view class="cu-tag badge" v-if="this.mynewpm!=0">
						<block v-if="this.mynewpm!=1">{{this.mynewpm>99?'99+':this.mynewpm}}</block>
					</view>
				</view>
				<text>@我</text>
			</view>
			<view class="cu-item" @click="messageclick">
				<view class="cuIcon-cardboardfill text-red">
					<view class="cu-tag badge" v-if="this.mynewpm!=0">
						<block v-if="this.mynewpm!=1">{{this.mynewpm>99?'99+':this.mynewpm}}</block>
					</view>
				</view>
				<text>招呼</text>
			</view>
			<view class="cu-item" @click="messageclick">
				<view class="cuIcon-cardboardfill text-red">
					<view class="cu-tag badge" v-if="this.mynewpm!=0">
						<block v-if="this.mynewpm!=1">{{this.mynewpm>99?'99+':this.mynewpm}}</block>
					</view>
				</view>
				<text>举报</text>
			</view>
		</view>


		<view class="cu-bar bg-white solid-bottom margin-top">
			<view class="action">
				<text v-if="mysetprompt>0" class="cuIcon-title text-orange"></text> 设置列表
			</view>
		</view>
		<view class="cu-list menu" :class="[menuBorder?'sm-border':'',menuCard?'card-menu margin-top':'']">
			<view class="cu-item" :class="menuArrow?'arrow':''">
				<view class="content">
					<text class="cuIcon-circlefill text-grey"></text>
					<text class="text-grey">我的积分</text>
				</view>
			</view>
			<view class="cu-item" :class="menuArrow?'arrow':''">
				<view class="content">
					<image src="/static/logo.png" class="png" mode="aspectFit"></image>
					<text class="text-grey">我的任务</text>
				</view>
			</view>
			<view class="cu-item" :class="menuArrow?'arrow':''">
				<button class="cu-btn content" open-type="contact">
					<text class="cuIcon-btn text-olive"></text>
					<text class="text-grey">风格设置</text>
				</button>
			</view>
			<view class="cu-item" :class="menuArrow?'arrow':''">
				<navigator class="content" hover-class="none" url="../list/list" open-type="redirect">
					<text class="cuIcon-discoverfill text-orange"></text>
					<text class="text-grey">个人资料</text>
				</navigator>
			</view>
			<view class="cu-item" :class="menuArrow?'arrow':''">
				<view class="content">
					<text class="cuIcon-emojiflashfill text-pink"></text>
					<text class="text-grey">好友</text>
				</view>
				<view class="action">
					<view class="cu-avatar-group">
						<view class="cu-avatar round sm"
							style="background-image:url(https://ossweb-img.qq.com/images/lol/web201310/skin/big10001.jpg);">
						</view>
						<view class="cu-avatar round sm"
							style="background-image:url(https://ossweb-img.qq.com/images/lol/web201310/skin/big81005.jpg);">
						</view>
						<view class="cu-avatar round sm"
							style="background-image:url(https://ossweb-img.qq.com/images/lol/web201310/skin/big25002.jpg);">
						</view>
						<view class="cu-avatar round sm"
							style="background-image:url(https://ossweb-img.qq.com/images/lol/web201310/skin/big91012.jpg);">
						</view>
					</view>
					<text class="text-grey text-sm">4 人</text>
				</view>
			</view>
			<view class="cu-item" :class="menuArrow?'arrow':''">
				<view class="content">
					<text class="cuIcon-btn text-green"></text>
					<text class="text-grey">头像</text>
				</view>
				<view class="action">
					<button class="cu-btn round bg-green shadow">
						<text class="cuIcon-upload"></text> 上传</button>
				</view>
			</view>
			<view class="cu-item" :class="menuArrow?'arrow':''">
				<view class="content">
					<text class="cuIcon-tagfill text-red  margin-right-xs"></text>
					<text class="text-grey">标签</text>
				</view>
				<view class="action">
					<view class="cu-tag round bg-orange light">音乐</view>
					<view class="cu-tag round bg-olive light">电影</view>
					<view class="cu-tag round bg-blue light">旅行</view>
				</view>
			</view>
			<view class="cu-item" :class="menuArrow?'arrow':''">
				<view class="content">
					<text class="cuIcon-warn text-green"></text>
					<text class="text-grey">密码安全</text>
				</view>
				<view class="action">
					<text class="text-grey text-sm">定期修改密码有助于论坛安全！</text>
				</view>
			</view>
			<view class="cu-item">
				<view class="content padding-tb-sm">
					<view>
						<text class="cuIcon-notification text-blue margin-right-xs"></text> 消息提醒
					</view>
					<view class="text-gray text-sm">
						<text class="cuIcon-infofill margin-right-xs"></text> 消息提醒还没打开！
					</view>
				</view>
				<view class="action">
					<switch @change="SwitchA" :class="switchA?'checked':''" :checked="switchA?true:false"></switch>
				</view>
			</view>
			<view class="cu-item" :class="menuArrow?'arrow':''">
				<button class="cu-btn content" open-type="contact" @tap="cleanlogin">
					<text class="cuIcon-footprint text-olive"></text>
					<text class="text-grey">注销登录</text>
				</button>
			</view>
			<view class="cu-item" :class="menuArrow?'arrow':''">
				<button class="cu-btn content" open-type="contact" @tap="checkupdate">
					<text class="cuIcon-info text-olive"></text>
					<text class="text-grey">版本信息</text>
				</button>
			</view>
			<view class="cu-item" :class="menuArrow?'arrow':''">
				<button class="cu-btn content" open-type="contact">
					<text class="cuIcon-infofill text-olive"></text>
					<text class="text-grey"></text>
				</button>
			</view>
		</view>
	</view>
</template>

<script>
	import Vue from 'vue'
	export default {
		data() {
			return {
				TabCur: 0,
				scrollLeft: 0,
				cuIconList: [{
					cuIcon: 'cardboardfill',
					color: 'red',
					badge: this.mynewpm,
					name: '消息'
				}, {
					cuIcon: 'recordfill',
					color: 'orange',
					badge: 1,
					name: '帖子'
				}, {
					cuIcon: 'picfill',
					color: 'yellow',
					badge: 0,
					name: '图像'
				}, {
					cuIcon: 'noticefill',
					color: 'olive',
					badge: this.mynewpm,
					name: '提醒'
				}, {
					cuIcon: 'upstagefill',
					color: 'cyan',
					badge: 0,
					name: '听众'
				}, {
					cuIcon: 'clothesfill',
					color: 'blue',
					badge: 0,
					name: '皮肤'
				}, {
					cuIcon: 'discoverfill',
					color: 'purple',
					badge: 0,
					name: '发现'
				}, {
					cuIcon: 'questionfill',
					color: 'mauve',
					badge: 0,
					name: '帮助'
				}, {
					cuIcon: 'commandfill',
					color: 'purple',
					badge: 0,
					name: '问答'
				}, {
					cuIcon: 'brandfill',
					color: 'mauve',
					badge: 0,
					name: '版权'
				}],
				switchA: false,
				avatarimgLoaded: false,
				modalName: null,
				skin: false,
				menuArrow: false,
				menuCard: false,
				menuBorder: false,
				listTouchStart: 0,
				mynewpm: 0,
				myinfoprompt: 0,
				mysetprompt: 0,
				mynewprompt: 0,
				listTouchDirection: null,
			};
		},
		methods: {
			cleanlogin() {
				uni.removeStorage({
					key: 'userlogininfo',
					success: function(res) {
						console.log('success');
						this.PageCur = 'login';
						uni.navigateTo({
						    url: '../../components/ay-login/login-password'
						});
					}
				});
			},
			messageclick(index) {
				console.log(index)
			},
			onSuccessImg() {
				this.avatarimgLoaded = true;
			},
			SwitchA(e) {
				this.switchA = e.detail.value
			},
			tabSelect(e) {
				this.TabCur = e.currentTarget.dataset.id;
				this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60
			},
			checkupdate(e) {
				this.switchA = e.detail.value
			},
		},
        created() {
			if (Vue.prototype.$token != '') {
				uni.request({
				    url: getApp().globalData.zddomain + 'plugin.php?id=xinxiu_network:user', //获取用户基本信息。
					method: 'GET',
					timeout: 10000,
				    data: {
						token: Vue.prototype.$token,
						action: 'user_info'
				    },
				    header: {
						'content-type': 'application/x-www-form-urlencoded' //自定义请求头信息
				    },
				    success: (res) => {
						var that = this;
						uni.getStorage({
							key: 'myuserinfo',
							success: function(res) {
								getApp().globalData.myusername = res.data.username;
								getApp().globalData.myadminid = res.data.adminid;
								getApp().globalData.mygroupid = res.data.groupid;
								getApp().globalData.mygroupexpiry = res.data.groupexpiry;
								getApp().globalData.myregdate = res.data.regdate;
								getApp().globalData.mycredits = res.data.credits;
								getApp().globalData.mynewpm = res.data.newpm;
								getApp().globalData.mynewprompt = res.data.newprompt;
								getApp().globalData.myfreeze = res.data.freeze;
								getApp().globalData.myinfoprompt = parseInt(res.data.newpm) + parseInt(res.data.newprompt);
								that.mynewpm = getApp().globalData.mynewpm;
								that.mynewprompt = getApp().globalData.newprompt;
								that.myinfoprompt = getApp().globalData.myinfoprompt;
								console.log(that.mynewpm);
								console.log(that.myinfoprompt);
							}
						});
				    }
				});
			}
        },
		onshow: function(){
		}
	}
</script>

<style>
	.page {
		height: 100vh;
	}
</style>
